.header {
    background-color: #282c34;
    font-size: calc(1rem + 2vmin);
    width: 100%;
    height: 60px;
    position: relative;

    &-tabs {
        list-style-type: none;
        display: flex;
        margin: 5px;
        padding: 5px;
        justify-content: end;
        li {
            padding: 5px;
            margin: 5px;
            a {
                text-decoration: none;
                color: inherit;
            }
        }
    }

    &-toggle {

        &-close,
        &-bar {
            display: none;
            position: absolute;
        }
    }
}

@media (max-width: 768px) {
    .header {
        position: relative;

        &-tabs {
            flex-direction: column;
            border-top: 1px solid cadetblue;
            margin: 10px;
            padding: 0;
            li {
                border-bottom: 1px solid cadetblue;
                padding-left: 0;
                margin: 0;
            }
        }

        &-toggle {
            position: absolute;
            right: 5px;
            width: 50px;
            height: 50px;
            padding: 5px;
            line-height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;

            &-close,
            &-bar {
                display: inline-block;
            }

            z-index: 1;
        }


        &-menu {
            background: inherit;
            // animation: 2s ease-out 0s 1 FadeIn;
            padding: 40px 0;
            // height: 0; /* move it horizontally off the page */
            // overflow: hidden;
            // transition: height 0.5s cubic-bezier(0.895, 0.03, 0.685, 0.22);

            width: 0;
            /* move it horizontally off the page */
            overflow: hidden;
            transition: width 1s ease-out;
            position: fixed;
            right: 0;
            margin-top: 10px;
        }

        &-opened {
            // height: 500px;
            // padding: 55px 20px;
            // transform: translateY(0);
            width: 100%;
        }

        // @keyframes FadeIn {
        //     0% {
        //         height: 0;
        //         padding: 0;
        //         overflow: hidden;
        //     }

        //     100% {
        //         height: auto;
        //     }
        // }

        .hidden {
            display: none;
        }
    }
}